body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header {  
    overflow: hidden;  
    background-color: rgba(17, 19, 34, 0.788);  
    padding: 20px 10px;  
    position: relative; 
    top: 0;  
    left:0; 
	  width: 100%;	
  }  
    
  .header a {  
    float: left;  
    color: white;  
    text-align: center;  
    padding: 12px;  
    text-decoration: none;  
    font-size: 18px;   
    line-height: 25px;  
    border-radius: 4px;  
    
  }  
  
  .header img {  
    width: 20%; 
    float: left;  
 }  
  
  .header a.logo {  
    font-size: 25px;  
    font-weight: bold;
    text-align: center;
    margin-left: -3.5%;  
    margin-top: 0.5%;
  }  
    
  .header a.active {  
    background: #5CDB95;  
    color: white;  
    font-weight: bold;  
  }  
    
  .header-right {  
    float: right;  
  }  
    
  .header .search-container {  
    float: left;  
  }  
    
  .header input[type=text] {  
    padding: 6px;  
    margin-top: 8px;
    font-size: 14px;  
    border: none;  
  }  
    
  .header .search-container button {  
    float: right;  
    padding: 6px 10px;  
    margin-top: 8px;  
    margin-right: 32px;  
    background: #ddd;  
    font-size: 14px;  
    border: none;  
    cursor: pointer;  
  }  
    
  .header .search-container button:hover {  
    background: #ccc;  
  }  
  
  @media screen and (max-width: 600px) {  
    
    .header img{  
        display: block;  
        float: none;  
        margin: auto;  
    }  
    .header a {  
      float: none;  
      display: block;  
      text-align: center;  
    } 
    
     
  }  
.menuBar{
    overflow: hidden;  
    background-color: rgba(66, 64, 64, 0.336);  
    width: 100%;
    border-left: none;
    border-right: none;
    box-shadow: 0 1px 12px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-bottom: 1%;
    font-size: 15px;
}

.signupB, .loginB{
    float: right;
    margin-right: 0.5em;
	box-shadow:inset 0px 1px 0px 0px #a6827e;
	background:-webkit-linear-gradient(top, #000000 5%, #9e9e9e 100%);
	background:linear-gradient(to bottom, #000000 5%, #9e9e9e 100%);
	background-color:#000000;
	border-radius:6px;
	border:1px solid black;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:12px 24px;
	text-decoration:none;
	text-shadow:0px 1px 50px #4d3534;

}
.signupB:hover, .loginB:hover, .addAdvertB:hover{
	background:-webkit-linear-gradient(top, #9e9e9e 5%, #000000 100%);
	background:linear-gradient(to bottom, #9e9e9e 5%, #000000 100%);
	background-color:#9e9e9e;
}
.signupB:active, .loginB:active, .addAdvertB:active {
    position:relative;
	top:1px;
}

.addAdvertB{
    float: left;
    margin-left: 0.5em;
	box-shadow:inset 0px 1px 0px 0px #a6827e;
	background:-webkit-linear-gradient(top, #000000 5%, #9e9e9e 100%);
	background:linear-gradient(to bottom, #000000 5%, #9e9e9e 100%);
	background-color:#000000;
	border-radius:6px;
	border:1px solid black;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:12px 24px;
	text-decoration:none;
	text-shadow:0px 1px 50px #4d3534;
}
.loggedUser{
    width: 55%; 
    max-height: 10px;
    float: right;
    text-align: right
}


* {
    box-sizing: border-box;
}


img {
    width: 100%;
    height: auto;
}

/* For mobile phones: */
.row::after {
    content: "";
    clear: both;
    display: table;
}
[class*="col-"] {
	width: 100%;
    float: left;
    padding: 15px;
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
	/* For tablets: */
    .col-xs-1 {width: 8.33%;}
    .col-xs-2 {width: 16.66%;}
    .col-xs-3 {width: 25%;}
    .col-xs-4 {width: 33.33%;}
    .col-xs-5 {width: 41.66%;}
    .col-xs-6 {width: 50%;}
    .col-xs-7 {width: 58.33%;}
    .col-xs-8 {width: 66.66%;}
    .col-xs-9 {width: 75%;}
    .col-xs-10 {width: 83.33%;}
    .col-xs-11 {width: 91.66%;}
    .col-xs-12 {width: 100%;}
	
	.myFont {font-size:12px;}
	
	h1 {font-size:26px;}
	
	.hide {display: none;}
} 


/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
	/* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}
	
	.myFont {font-size:14px;}
	h1 {font-size:30px;}
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
	/* For tablets: */
    .col-m-1 {width: 8.33%;}
    .col-m-2 {width: 16.66%;}
    .col-m-3 {width: 25%;}
    .col-m-4 {width: 33.33%;}
    .col-m-5 {width: 41.66%;}
    .col-m-6 {width: 50%;}
    .col-m-7 {width: 58.33%;}
    .col-m-8 {width: 66.66%;}
    .col-m-9 {width: 75%;}
    .col-m-10 {width: 83.33%;}
    .col-m-11 {width: 91.66%;}
    .col-m-12 {width: 100%;}
	
	.myFont {font-size:16px;}
	h1 {font-size:34px;}
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
	/* For tablets: */
    .col-l-1 {width: 8.33%;}
    .col-l-2 {width: 16.66%;}
    .col-l-3 {width: 25%;}
    .col-l-4 {width: 33.33%;}
    .col-l-5 {width: 41.66%;}
    .col-l-6 {width: 50%;}
    .col-l-7 {width: 58.33%;}
    .col-l-8 {width: 66.66%;}
    .col-l-9 {width: 75%;}
    .col-l-10 {width: 83.33%;}
    .col-l-11 {width: 91.66%;}
    .col-l-12 {width: 100%;}
	
	.myFont {font-size:18px;}
	h1 {font-size:38px;}
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
	/* For tablets: */
    .col-xl-1 {width: 8.33%;}
    .col-xl-2 {width: 16.66%;}
    .col-xl-3 {width: 25%;}
    .col-xl-4 {width: 33.33%;}
    .col-xl-5 {width: 41.66%;}
    .col-xl-6 {width: 50%;}
    .col-xl-7 {width: 58.33%;}
    .col-xl-8 {width: 66.66%;}
    .col-xl-9 {width: 75%;}
    .col-xl-10 {width: 83.33%;}
    .col-xl-11 {width: 91.66%;}
    .col-xl-12 {width: 100%;}
	
	.myFont {font-size:20px;}
	h1 {font-size:42px;}
}
