* {
    box-sizing: border-box;
}

.addAdvertForm{
    background-color: lightgray;
    width: 50%;
    margin: 4% auto;
    padding: 2%;
    border-radius: 5px;
    box-shadow: 0 1px 12px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
}
#title, #category, #ItemCondition, #askingPrice, #city, #description, #message {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    box-sizing: border-box;
}
#category:focus, #ItemCondition:focus, #description:focus, #message:focus{
    box-shadow: 0 1px 12px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    outline: none;
    border: 1px solid black;
}

#photo{
    width: 100%;
    padding: 2%;
    background-color: white;
    border:1px solid rgba(156, 154, 154, 0.897);
}
.btn {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin-top: 5%;
    border: none;
    cursor: pointer;
    width: 100%;
}
.btn:hover {
    font-weight: bold;
    color: black;
    box-shadow: 0 1px 12px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
}
.succesMsg{
    text-align: center;
    color: green;
}